<template>
<div>
  <div id="map"></div>
  <div style="display:none;">
    <!-- 控件 -->
    <div id="screen-center-coordinate" class="screen-center-coordiante">{{ScreenCenter}}</div>

    <!-- 图标 -->
    <el-popover
      :id="item.id"
      v-show="isSchoolShown"
      v-for="item in sites"
      :key="item.id"
      :title="item.cnName"
      placement="top"
      width="250"
      trigger="hover">
      <div>{{item.introduction}}<a :href="item.webSite" target="_blank">..前往官网</a></div>
      <div slot="reference" class="marker">
        {{item.cnName}}
      </div>
    </el-popover>
  </div>
</div>
</template>

<script>
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import $ from 'jquery';

import {defaults as defaultControls,
        MousePosition} from 'ol/control';
import Control from 'ol/control/Control';
import {toStringXY} from 'ol/coordinate';
import Overlay from 'ol/Overlay';

export default {
  name: 'popup-markup',
  watch: {
    zoomValue: function (val, oldVal) {
      if (val !== oldVal) {
        if (val < 11 || val > 16) {
          this.isSchoolShown = false;
        } else {
          this.isSchoolShown = true;
        }
      }
    }
  }, // watch
  data() {
    return {
      map: {},
      view: {},
      layers: [],
      zoomValue: 13,

      screenCenter: [],

      // 地点
      sites: {
        WHU: {
          id: 'whu',
          coordinate: [114.360809, 30.537100],
          cnName: '武汉大学',
          introduction: '江城多山，珞珈独秀；山上有黉，武汉大学。武汉大学是国家教育部直属重点综合性大学，是国家“985工程”和“211工程”重点建设高校，是首批“双一流”建设高校。',
          webSite: 'http://www.whu.edu.cn/',
        },
        ZUEL_SY: {
          id: 'zuel-sy',
          coordinate: [114.307938, 30.537701],
          cnName: '中南财经政法大学（首义校区）',
          introduction: '中南财经政法大学是中华人民共和国教育部直属的一所以经济学、法学、管理学为主干...首义校区位于历史悠久的黄鹤楼下。',
          webSite: 'http://www.zuel.edu.cn/',
        },
        ZUEL_NH: {
          id: 'zuel-nh',
          coordinate: [114.379692, 30.476246],
          cnName: '中南财经政法大学（南湖校区）',
          introduction: '中南财经政法大学是中华人民共和国教育部直属的一所以经济学、法学、管理学为主干...南湖校区位于风景秀丽的南湖水畔。',
          webSite: 'http://www.zuel.edu.cn/',
        }
      },
      isSchoolShown: true
    }
  }, // data
  computed: {
    ScreenCenter: function () {
      const center = this.screenCenter;
      if (center.length !== 0) {
        return 'Center Coordinate: ( ' + toStringXY(center, 6) + ' )'
      } else return '';
    }
  }, // computed
  methods: {
    setMarker() {
      const sites = this.sites;
      for(let item in sites) {
        let markerElm = $('#' + sites[item].id)[0];
        let coordinate = sites[item].coordinate;

        this.map.addOverlay(
          new Overlay({
            position: coordinate,
            positioning: 'center-center',
            element: markerElm
          })
        )
      }
    },
    preDefine() {
      // view
      this.view = new View({
        center: [114.3569, 30.5141],
        zoom: this.zoomValue,
        projection: 'EPSG:4326',
      });
      // layers
      this.layers = [
        new TileLayer({
          source: new OSM()
        }),
      ]
    },
    createMap() {
      this.map = new Map({
        controls: defaultControls({
          attribution: false
        }),
        target: 'map',
        view: this.view,
        layers: this.layers
      });
      // 监听鼠标点击事件
      this.map.on('click', e => {
        // 显示坐标
        let coordinate = toStringXY(e.coordinate, 6);
        let zoom = e.map.getView().getZoom();
        this.$notify({
          message: coordinate + ', ' + zoom,
          type: 'success'
        });
        console.log(coordinate);
      });

      // 动态显示屏幕中心坐标
      this.map.on('postrender', e => {
        this.screenCenter = e.map.getView().getCenter();
        this.zoomValue = e.map.getView().getZoom();
      });

      // 添加自定义控件
      this.map.addControl(
        new Control({
          element: $('#screen-center-coordinate')[0]
        })
      );
    }
  }, // methods
  created() {
    
  }, // created
  mounted() {
    // 获取屏高方法
    let h = $('.content-box').css('height');
    h = (h.split('px')[0]).toString() + 'px';
    $('#map').css('height', h);
    this.preDefine();
    this.createMap();
    this.setMarker();
  }
}
</script>

<style>
#map {
  height: 100%;
  width: 100%;
}
.screen-center-coordiante {
  bottom: 8px;
  right: 8px;
  position: absolute;
  font-weight: bold;
}
.marker {
  display: inline-block;
  width: 100px;
  margin: 3px 5px;
  cursor: pointer;

  text-decoration: none;
  color: #a30;
  font-size: 12px;
  font-weight: bold;
  text-shadow: #fc3 0.1em 0.1em 0.2em;
}
</style>